@function randomNum($min, $max) {
	$rand: random();
	$randomNum: $min + floor($rand * (($max - $min) + 1));
	@return $randomNum;
}

$blobColor: #242424;

.blob-header {
	filter: url(#blob);
	display: flex;
	align-items: center;
	justify-content: center;

	& + .hide {
		display: none;
	}

	& .blob-content {
		position: relative;
		z-index: 1;
		top: 39px;
		padding: 7px;
	}

	& > div {
		position: absolute;
		display: inline-block;
		background: $blobColor;
		box-shadow: 0px 0px 20px $blobColor;
		border-radius: 500px;
		$top-collection: 0;
		$left-collection: 0;

		@for $i from 1 to 30 {
			$left: randomNum(-150px, 50px);
			$top: randomNum(-250px, 100px);
			$randW: randomNum(150px, 180px);
			$randH: randomNum(100px, 250px);

			&:nth-child(#{$i}) {
				width: $randW;
				height: $randH;
				left: $left;
			}

			$left-collection: append($left-collection, $left);
			$top-collection: append($top-collection, $top);
		}

		@for $i from 1 to length($top-collection) {
			&:nth-child(#{$i}) {
				left: nth($left-collection, $i);
				top: nth($top-collection, $i);

				animation: anim-#{$i} 8s linear infinite;
				animation-direction: normal;
				transform-origin: center center;

				animation-delay: -#{$i/1}s;

				@keyframes anim-#{$i} {
					0% {
						transform: rotate(0deg);
						left: 0;
						top: 0;
					}
					50% {
						left: nth($left-collection, $i);
						top: nth($top-collection, $i);
					}
					100% {
						transform: rotate(-360deg);
						left: 0;
						top: 0;
					}
				}
			}
		}
	}
}

