html {
  overflow: hidden; }

.App {
  display: flex;
  justify-content: space-between;
  height: 100vh;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  background: -webkit-radial-gradient(top left, circle, #b3ff00, #047baa);
  background: radial-gradient(circle at top left, #b3ff00, #047baa);
  overflow: hidden; }
  .App .face {
    border-radius: 50%;
    -webkit-transform: rotate(-8deg);
            transform: rotate(-8deg); }
  .App nav {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    text-align: center;
    position: relative; }
    .App nav a {
      width: 100%;
      padding: 5px;
      font-size: 18px;
      text-decoration: none;
      color: black; }
  .App .app-header .name {
    margin: 15px 0px 100px 0px;
    height: 100%;
    width: 250px; }
  .App .app-header nav {
    top: 30px; }
  .App .app-header a {
    font-weight: 400;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    border-radius: 5px;
    text-transform: uppercase;
    margin: 5px; }
    .App .app-header a:hover {
      background: #2424241c; }
  .App header {
    margin: 0 10% 0 20%;
    padding: 10px;
    width: 250px;
    min-width: 250px;
    text-align: center;
    -webkit-transition: all 0.25s;
    transition: all 0.25s;
    padding-top: 10%; }
    .App header h1 {
      color: #252525; }
    .App header.show {
      margin: 0 4%; }
  .App .page-content-wrap {
    display: flex;
    color: black;
    width: 0%;
    -webkit-transition: width 0.5s;
    transition: width 0.5s; }
    .App .page-content-wrap.show {
      width: 100%; }
    .App .page-content-wrap .page-content {
      background: white;
      height: auto;
      width: auto;
      padding: 15px 45px;
      padding-top: 2% !important;
      box-shadow: -5px -5px 20px #00000059;
      border: 0px solid black;
      overflow: auto;
      overflow-x: hidden;
      flex-grow: 1; }

